/* eslint no-unused-vars: 0 */

import Form from "antd/lib/form";
import PropTypes from "prop-types";
import React from "react";

import "antd/lib/form/style/index.css";
import "antd/lib/input/style/index.css";
import "antd/lib/button/style/index.css";
import { ThemeContext } from "../../layouts";

const Contact = props => {
  function myFunction() {
    var copyText = document.getElementById("myInput");
    copyText.select();
    copyText.setSelectionRange(0, 99999);
    document.execCommand("copy");
    var btn = document.getElementById("myCopy");
    btn.innerHTML = "Copied";
  }

  function outFunc() {
    var btn = document.getElementById("myCopy");
    btn.innerHTML = "Copy";
  }
  return (
    <React.Fragment>
      <ThemeContext.Consumer>
        {theme => (
          <div>
            <a href="https://github.com/JunMock/">GitHub - JunMock</a>
            <a href="mailto:kjmzzang01@gmail.com" id="myInput" style="width:auto">
              kjmzzang01@gmail.com
            </a>

            <button id="myCopy" onclick={myFunction()} onmouseout={outFunc()}>
              Copy
            </button>
          </div>
        )}
      </ThemeContext.Consumer>
    </React.Fragment>
  );
};

Contact.propTypes = {
  form: PropTypes.object
};

const ContactForm = Form.create({})(Contact);

export default ContactForm;
